export default {
  name: 'AdminPagination',
  props: {
    storeAction: {
      type: String,
      default: '',
    },
    storeGetter: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      current_page: 1,
      last_page: '',
      loading: false,
    }
  },
  computed: {
    currentObject() {
      return this.$store.getters[`${this.storeGetter}`]
    },
  },
  methods: {
    async changePage(direction) {
      if (this.loading === true) return false
      this.loading = true
      switch (direction) {
        case 'next':
          this.current_page++
          break
        case 'prev':
          this.current_page--
          break
        case 'first':
          this.current_page = 1
          break
        case 'last':
          this.current_page = this.currentObject.meta.last_page
          break
        default:
          break
      }

      await this.$store.dispatch(this.storeAction, this.current_page)
      setTimeout(() => {
        this.loading = false
      }, 2000)
    },
  },
}
